/* Orders Page Styling */
.orders-page {
  font-family: 'Poppins', sans-serif;
  background: #f8f9fc;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.orders-page h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #2d2d2d;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;
}

/* No Orders Section */
.orders-page p {
  font-size: 1.2rem;
  color: #a5a5a5;
  text-align: center;
  margin: 30px 0;
}

/* Orders List */
.orders-list {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
  padding: 0 20px;
}

/* Order Card Styling */
.order-card {
  background: #fff;
  border-radius: 15px;
  padding: 0 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
   
}

.order-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.15);
}


.order-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background: linear-gradient(135deg, #ff6b6b, #f7c7c7);
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.order-card:hover::before {
  transform: scaleY(1);
}

/* Order Summary (Order ID and Item) */
.order-summary {
  cursor: pointer;
}

.order-summary h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 15px;
}

.order-item {
  display: flex;
  align-items: center;
  gap: 20px;
}

.order-item img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
}

.item-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: calc(100% - 100px);
}

.item-info p {
  color: #1d1d1d;
  margin: 3px 0;
}

.item-info p:last-child {
  font-weight: 600;
  color: #000;
}

/* Order Details (Initially hidden) */
.order-details {
  margin-top: 0px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 1rem;
  color: #363636;
}

.order-details p {
  margin: 8px 0;
  color: #3f3f3f;
}

.order-items {
  margin-top: 15px;
}

.order-item img {
  width: 100px;
  height: 100px;
}

.item-info p {
  font-size: 1.1rem;
}

.item-info p:last-child {
  font-weight: 600;
  color: #000;
}

.order-details .order-items h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #444;
}

@media (max-width: 480px) {
  .orders-page {
    padding: 10px;
  }

  .orders-page h1 {
    font-size: 1.8rem;
    margin-bottom: 15px;
  }

  /* No Orders Message */
  .orders-page p {
    font-size: 0.95rem;
    margin: 5px 0;
  }

  /* Orders List */
  .orders-list {
     display: flex;
     justify-content: center;
     align-items: center;
    gap: 10px;
    padding: 0 0px;
    border-radius: 5px;
    margin-top: 3%;
  }

  /* Order Card */
  .order-card {
    padding: 10px;
    border-radius: 8px;
  }

  .order-summary h3 {
    font-size: 1.1rem;
    margin-bottom: 8px;
  }

  /* Order Item */
  .order-item {
    gap: 10px;
    flex-direction: row; /* Stack image and details */
    align-items: flex-start;
  }

  .order-item img {
    width: 100px;
    height: 100px;
    border-radius: 6px;
  }

  .item-info {
    width: 100%;
  }

  .item-info p {
    font-size: 0.9rem;
  }

  /* Order Details */
  .order-details {
    padding: 0;
    font-size: 0.85rem;
  }

  .order-details p {
    margin: 5px 0;
  }

  .order-items {
    margin-top: 8px;
  }

  .order-items h4 {
    font-size: 0.95rem;
  }
}
